.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: @header-index;
  background-color: @standart-background-color;

  &--no-bg {
    background-color: transparent;
  }

  &--sticky {
    position: sticky;
  }
}

.header__wrap {
  position: relative;
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  padding: @basic-side-padding;

  @media (max-width: @tablet-width) {
    grid-template-columns: 1fr min-content;
    padding: 0 @basic-side-padding;
  }
}

.header__title {
  z-index: @header-title-index;

  font-family: @basic-font;
  font-size: @header-title-font-size;
  font-weight: @header-title-font-weight;
  color: @standart-text-color;
  text-transform: uppercase;
  transition: all .3s ease;

  &--index-page {
    color: @standart-white;
  }

  &--menu-open { // in index.html change color when mobile menu is open
    color: @standart-text-color;
    transition: all .3s ease;
  }
}

.header__menu-button {
  width: @header-mobile-btn-size;
  height: @header-mobile-btn-size;
  border: none;
  background-color: transparent;
  cursor: pointer;

  display: none;

  @media (max-width: @tablet-width) {
    z-index: @header-menu-btn-index;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.nav {
  display: flex;
  justify-content: end;
  align-items: center;

  @media (max-width: @tablet-width) {
    position: absolute;
    width: 100%;
    top: -200%;

    transition: all .5s ease-in;
  }

  &--open {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    transition: all 0.5s ease;
    justify-content: center;
    background-color: @standart-background-color;
  }

  &--open .nav__list {
    padding: @header-mobile-btn-size @basic-side-padding;
    pointer-events: all;
    opacity: 1;
    transition: all 0.5s ease;
  }

  &--open .nav__link {
    color: @standart-text-color;

    &::after {
      background-color: @standart-text-color;
    }
  }
}

.nav__list {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 20px;

  list-style: none;

  @media (max-width: @tablet-width) {
    flex-direction: column;
    justify-content: start;
    align-items: center;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
  }
}

.nav__item {
  @media (max-width: @tablet-width) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}

.nav__link {
  padding: 8px 0;
  position: relative;

  font-family: @basic-font;
  color: @standart-text-color;
  font-weight: @header-link-font-weight;
  font-size: @header-link-font-size;

  text-decoration: none;
  text-transform: capitalize;
  user-select: none;

  opacity: 0.7;
  transition: .2s all ease-in;

  &::after { // for active and focused links
    display: none;
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: @standart-text-color;
    transition: .2s all ease-in;
    border-radius: 20%;
  }

  &:hover,
  &:focus {
    opacity: 1;
    transition: .2s all ease-in;

    &::after {
      display: block;
    }
  }

  &--index-page {
    color: @standart-white;

    &::after {
      background-color: @standart-white;
    }
  }

  &--current {
    opacity: 1;
    pointer-events: none;

    &::after {
      display: block;
    }
  }

  @media (max-width: @tablet-width) {
    width: 100%;
    font-size: @header-link-tablet-font-size;
    text-align: center;
  }
}
